@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?fy6cjq');
  src:  url('fonts/icomoon.eot?fy6cjq#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?fy6cjq') format('truetype'),
    url('fonts/icomoon.woff?fy6cjq') format('woff'),
    url('fonts/icomoon.svg?fy6cjq#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  
  /* Enable Ligatures ================ */
  letter-spacing: 0;
  -webkit-font-feature-settings: "liga";
  -moz-font-feature-settings: "liga=1";
  -moz-font-feature-settings: "liga";
  -ms-font-feature-settings: "liga" 1;
  font-feature-settings: "liga";
  -webkit-font-variant-ligatures: discretionary-ligatures;
  font-variant-ligatures: discretionary-ligatures;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

